#resume {
  display: flex;
  flex-wrap: wrap;
}

.resume__editor, .resume__content {
  flex-basis: 0;
  flex-grow: 1;
  height: 100vh;
  align-items: center;
}

.resume__editor {
  min-width: 0px;
  width: 0px;
  max-width: 0px;
  background-color: #eaf8f8;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: space-between;
  overflow: hidden;
  transition: all 650ms cubic-bezier(0.24, 0.26, 0.245, 1.17);
}

.resume__editor__content, .resume__editor__header, .resume__editor__footer {
  flex: 0 1;
  width: 100%;
  min-width: 400px;
  transform: translatex(-100px);
  transition: all 850ms 0ms ease;
}

.resume__editor__header {
  text-align: center;
  padding: 32px;
}

.resume__editor__header .editor__section {
  color: #2c8ddd;
}

.resume__editor__header hr {
  width: 64px;
  border-color: #a9daf4;
}

.resume__editor__header .editor__section__dots {
  margin-top: 24px;
  margin-bottom: 24px;
  text-align: center;
}

.resume__editor__header .editor__section__dots li {
  display: inline-block;
  position: relative;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  text-align: center;
  margin: 0 8px;
  font-family: "soleil", "source-sans-pro", -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  font-size: 0.875rem;
  line-height: 32px;
  font-weight: 700;
  color: #a9daf4;
  vertical-align: middle;
  cursor: pointer;
  transition: all 150ms;
}

.resume__editor__header .editor__section__dots li:after {
  content: '';
  position: absolute;
  left: 4px;
  top: 4px;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  z-index: -1;
  transition: all 150ms ease-out;
}

.resume__editor__header .editor__section__dots li:hover {
  color: #2c8ddd;
}

.resume__editor__header .editor__section__dots li:hover:after {
  background-color: rgba(169, 218, 244, 0.25);
}

.resume__editor__header .editor__section__dots li.-active {
  color: #fff;
}

.resume__editor__header .editor__section__dots li.-active:after {
  top: 0;
  left: 0;
  width: 32px;
  height: 32px;
  background-color: #f4725d;
}

.resume__editor__header .editor__progress {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background-color: transparent;
  border-radius: 0px;
}

.resume__editor__header .editor__progress .progress-bar {
  border-radius: 0px 3px 3px 0px;
  height: 6px;
  transition: width 850ms;
}

.resume__editor__footer {
  padding: 16px;
  height: 68px;
  text-align: center;
}

.resume__editor__footer .editor__back-btn, .resume__editor__footer .editor__next-btn {
  display: inline-block;
  height: 36px;
  width: 36px;
  border-radius: 36px;
  background-color: #a9daf4;
  color: #fff;
  cursor: pointer;
  text-transform: uppercase;
  font-family: "soleil", "source-sans-pro", -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  line-height: 36px;
  vertical-align: middle;
  font-size: 0.875rem;
  font-weight: 700;
  text-align: center;
  letter-spacing: .05em;
  border: 1px solid #a9daf4;
  overflow: hidden;
}

.resume__editor__footer .editor__back-btn:hover, .resume__editor__footer .editor__next-btn:hover {
  background-color: #2c8ddd;
}

.resume__editor__footer .editor__back-btn .i--arrow, .resume__editor__footer .editor__next-btn .i--arrow {
  display: inline;
  background-position: center right;
  background-repeat: no-repeat;
  background-size: auto 8px;
  text-align: center;
  display: inline-block;
  width: 16px;
  height: 10px;
  overflow: hidden;
  line-height: inherit;
  margin: 0 9px;
  transition: all 350ms;
}

.resume__editor__footer .editor__back-btn .i--arrow {
  background-image: url("../../img/UI/arrow-left-lighter.svg");
  background-position: center left;
}

.resume__editor__footer .editor__next-btn {
  text-align: right;
  white-space: nowrap;
  overflow: hidden;
  direction: rtl;
  text-align: center;
}

.resume__editor__footer .editor__next-btn .i--arrow {
  background-image: url("../../img/UI/arrow-left-lighter.svg");
  background-position: center right;
}

.resume__editor__footer .editor__next-btn.-closing {
  animation: next-btn-close 650ms;
}

.resume__editor__footer .editor__next-btn:after {
  content: 'next';
  position: relative;
  display: inline-block;
  opacity: 0;
  top: 36px;
  transition: opacity 300ms 300ms, top 0ms 600ms;
}

.resume__editor__footer .editor__next-btn.-disabled {
  background-color: transparent;
  cursor: default;
}

.resume__editor__footer .editor__next-btn.-disabled .i--arrow {
  background-image: url("../../img/UI/arrow-right-light.svg");
  transition: width 200ms 350ms, background 200ms 350ms, margin 200ms;
}

.resume__editor__footer .editor__next-btn.-active {
  line-height: 46px;
  height: 46px;
  width: 250px;
  border-radius: 46px;
  background-color: #2c8ddd;
  margin-top: -5px;
  margin-right: -5px;
  animation: next-btn-active 650ms normal;
}

.resume__editor__footer .editor__next-btn.-active .i--arrow {
  background-image: url("../../img/UI/arrow-right-lighter.svg");
  transition: width 350ms 300ms, background 200ms, margin 200ms;
  width: 36px;
}

.resume__editor__footer .editor__next-btn.-active:hover {
  background-color: #005596;
}

.resume__editor__footer .editor__next-btn.-active:hover .i--arrow {
  width: 48px;
  transition: width 250ms;
}

.resume__editor__footer .editor__next-btn.-active:after {
  top: 0;
  opacity: 1;
  transition: all 550ms 300ms;
}

@keyframes next-btn-close {
  0% {
    height: 46px;
    width: 250px;
    border-radius: 46px;
    background-color: #2c8ddd;
    margin-top: -5px;
    margin-right: -5px;
    line-height: 46px;
  }
  40% {
    height: 46px;
    line-height: 46px;
    width: 250px;
    border-radius: 46px;
    margin-top: -5px;
    margin-right: -5px;
    background-color: #a9daf4;
  }
  50% {
    height: 36px;
    line-height: 36px;
    width: 240px;
    border-radius: 36px;
    margin-top: 0px;
    margin-right: 0px;
  }
  80% {
    width: 36px;
    height: 36px;
    line-height: 36px;
    margin-top: 0px;
    margin-right: 0px;
  }
  100% {
    width: 36px;
    height: 36px;
    line-height: 36px;
    margin-top: 0px;
    margin-right: 0px;
  }
}

@keyframes next-btn-active {
  0% {
    width: 36px;
    height: 36px;
    line-height: 36px;
    border-radius: 36px;
    margin-top: 0px;
    margin-right: 0px;
  }
  50% {
    height: 46px;
    width: 46px;
    line-height: 46px;
    border-radius: 46px;
    background-color: #2c8ddd;
    margin-top: -5px;
    margin-right: -5px;
  }
  100% {
    height: 46px;
    width: 250px;
    border-radius: 46px;
    background-color: #2c8ddd;
    margin-top: -5px;
    margin-right: -5px;
  }
}

.resume__editor__content {
  width: 400px;
  transition: all 700ms 0ms;
  opacity: 0;
  position: relative;
}

.resume__editor__content .intro-photo {
  margin-top: -100px;
}

.resume__editor__content .intro-photo + div {
  margin-top: -32px;
}

.resume__editor__content .btn {
  padding: 16px;
  font-size: 0.7518796992rem;
  cursor: pointer;
  background: transparent;
  border-color: #a9daf4;
  width: 100%;
  position: relative;
  border-radius: 8px;
}

.resume__editor__content .btn:after {
  content: "";
  display: block;
  position: absolute;
  left: 12px;
  top: 12px;
  width: 24px;
  height: 24px;
  background-image: url("../../img/UI/check-circle-gummy.svg");
  transform: translateX(-16px) scale(0.5);
  transition: all 250ms cubic-bezier(0.24, 0.26, 0.245, 1.17);
  visibility: hidden;
  opacity: 0;
}

.resume__editor__content .btn:hover {
  background-color: transparent;
  border-color: #2c8ddd;
  color: #2c8ddd;
}

.resume__editor__content .btn.-selected {
  border-color: #f4725d;
  color: #f4725d;
  padding-left: 42px;
}

.resume__editor__content .btn.-selected:after {
  opacity: 1;
  visibility: visible;
  transform: translateX(0px) scale(1);
}

.editor__question {
  visibility: visible;
  opacity: 1;
  position: relative;
  transform: translateX(0px);
  transition: all 450ms 250ms;
}

.editor__question.-out {
  opacity: 0;
  visibility: hidden;
  transform: translateX(-500px);
  position: absolute;
  transition: all 650ms;
  top: 0;
}

.editor__question.-incoming {
  opacity: 0;
  visibility: hidden;
  transform: translateX(500px);
  position: absolute;
  top: 0;
}

.resume__content {
  max-width: 100%;
  overflow-y: scroll;
  position: relative;
}

#resume.edit-mode .resume__editor {
  min-width: 480px;
  max-width: 620px;
}

#resume.edit-mode .resume__editor > div {
  transform: translatex(0px);
  transition: all 850ms 200ms cubic-bezier(0.24, 0.26, 0.245, 1.17);
}

#resume.edit-mode .resume__editor .resume__editor__content {
  transform: translateY(0px);
  opacity: 1;
}

.edit-btn {
  position: absolute;
  left: 0px;
  bottom: 0px;
  margin: 15px;
}

html {
  -webkit-font-smoothing: antialiased;
}
